.App {
   text-align: center;
}

.App-logo {
   height: 40vmin;
   pointer-events: none;
}
a {
   appearance: none;
   text-decoration: none !important;
}
th {
   color: #48a3f7;
}
.btn {
   background-color: #48a3f7;
   border-color: #48a3f7;
   color: white;
}
.btn:hover {
   background-color: #48a3f7;
   border-color: #48a3f7;
   color: white;
}
.btn:focus {
   background-color: #48a3f7;
   border-color: #48a3f7;
}

@media (prefers-reduced-motion: no-preference) {
   .App-logo {
      animation: App-logo-spin infinite 20s linear;
   }
}

.App-header {
   background-color: #282c34;
   min-height: 100vh;
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   font-size: calc(10px + 2vmin);
   color: white;
}

.App-link {
   color: #61dafb;
}

@keyframes App-logo-spin {
   from {
      transform: rotate(0deg);
   }
   to {
      transform: rotate(360deg);
   }
}
